/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable react/jsx-props-no-spreading */
import {
  Box,
  Breadcrumbs,
  CardMedia,
  Input,
  Link,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from '@mui/material';

import { useCallback, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styles from './styles.module.scss';
import { Space } from '../../entities';

export default function Spaces() {
  const navigate = useNavigate();
  const [iSpace, setISpace] = useState<Space[]>([]);

  const places = useCallback(async () => {
    const response = await fetch('https://zenuki.kz/api/v1/space', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    });

    if (!response.ok) {
      throw new Error('Failed to fetch spaces');
    }

    const data = await response.json();
    setISpace(data);
  }, []);

  useEffect(() => {
    setISpace([]);
    places();
  }, [places]);

  return (
    <Box component="section" className={styles.profile}>
      <div className="container">
        <Box component="div" className={styles.profile__wrapper}>
          <Box>
            <Breadcrumbs aria-label="breadcrumb">
              <Link
                underline="hover"
                color="inherit"
                onClick={() => navigate('/')}
              >
                Home
              </Link>
              <Typography sx={{ color: 'text.primary' }}>Spaces</Typography>
            </Breadcrumbs>
            <Box
              sx={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
              }}
            >
              <Typography sx={{ fontSize: '36px', fontWeight: '600' }}>
                Spaces
              </Typography>
              <Input
                placeholder="Search"
                disableUnderline
                sx={{
                  border: '1px solid gray',
                  padding: '5px 20px',
                  borderRadius: '20px',
                }}
              />
            </Box>
          </Box>
          <TableContainer component={Paper}>
            <Table sx={{ minWidth: 650 }} aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell />
                  <TableCell align="left">Name</TableCell>
                  <TableCell align="right">Location</TableCell>
                  <TableCell align="right">Address</TableCell>
                  <TableCell align="right">Size</TableCell>
                  <TableCell align="right">Max. Capacity</TableCell>
                  <TableCell align="right">Base Rental Cost</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {iSpace.map((row) => (
                  <TableRow
                    key={row.name}
                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                  >
                    <TableCell
                      component="th"
                      scope="row"
                      sx={{ width: '350px' }}
                    >
                      <CardMedia
                        component="img"
                        image={`https://zenuki.kz/api/v1/files/${row.imageUrl}`}
                        sx={{
                          objectFit: 'cover',
                          height: '200px',
                        }}
                        alt={row.name}
                      />
                    </TableCell>
                    <TableCell align="left">{row.name}</TableCell>
                    <TableCell align="right">{row.location}</TableCell>
                    <TableCell align="right">{row.address}</TableCell>
                    <TableCell align="right">{row.size}</TableCell>
                    <TableCell align="right">{row.maxCapacity}</TableCell>
                    <TableCell align="right">{row.baseRentalCost}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Box>
      </div>
    </Box>
  );
}
