import { useCallback, useEffect, useState } from 'react';
import { Box } from '@mui/material';
import { Space } from '../../../entities';
import InteriorItem from '../../../shared/ui/Card/InteriorItem';

export default function AllSpace() {
  const [spaces, setSpaces] = useState<Space[]>([]);

  const places = useCallback(async () => {
    const response = await fetch('https://zenuki.kz/api/v1/space', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    });

    if (!response.ok) {
      throw new Error('Failed to fetch spaces');
    }

    const data = await response.json();
    setSpaces(data);
  }, []);

  useEffect(() => {
    setSpaces([]);
    places();
  }, [places]);

  return (
    <Box
      sx={{
        display: 'grid',
        gridTemplateColumns: 'repeat(3, 1fr)',
        justifyContent: 'space-between',
        gap: 4,
      }}
    >
      {spaces.map((place) =>
        place.imageUrl ? (
          <InteriorItem key={place.id} interior={place} updateQuery={places} />
        ) : null
      )}
    </Box>
  );
}
