/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable no-use-before-define */
import {
  Avatar,
  Box,
  Breadcrumbs,
  Button,
  Input,
  InputLabel,
  Link,
  Modal,
  Paper,
  Typography,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { useEffect, useState } from 'react';
import styles from './styles.module.scss';
import { useUserStore } from '../../app/store/useUserStore';

export default function Profile() {
  const { user, isAuthorized, token, refreshToken } = useUserStore();
  const { login: userLogin } = useUserStore();
  const navigate = useNavigate();
  const [open, setOpen] = useState(false);
  const [editUser, setEditUser] = useState({ ...user });

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handleChange = (event: { target: { name: any; value: any } }) => {
    const { name, value } = event.target;
    setEditUser({ ...editUser, [name]: value });
  };

  const handleSave = async () => {
    try {
      const response = await fetch(
        'https://server.kenuki.org/api/user/profile-update',
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${token}`, // Replace with actual token if stored elsewhere
          },
          body: JSON.stringify({
            phone: editUser.phone_number,
            firstName: editUser.first_name,
            lastName: editUser.last_name,
          }),
        }
      );

      console.log(response);

      if (!response.ok) {
        throw new Error('Failed to update profile');
      }
      const data = await response.json();
      const { accessToken } = data;

      handleClose();

      if (accessToken && refreshToken) {
        await userLogin(accessToken, refreshToken);
        navigate('/profile');
      }
    } catch (error) {
      console.error('Error updating profile:', error);
    }
  };

  useEffect(() => {
    if (!isAuthorized) {
      navigate('/login');
    }
  }, [isAuthorized, navigate]);

  return (
    <Box component="section" className={styles.profile}>
      <div className="container">
        <Box component="div" className={styles.profile__wrapper}>
          <Box>
            <Breadcrumbs aria-label="breadcrumb">
              <Link
                underline="hover"
                color="inherit"
                onClick={() => navigate('/')}
              >
                Home
              </Link>
              <Typography sx={{ color: 'text.primary' }}>Profile</Typography>
            </Breadcrumbs>
            <Typography sx={{ fontSize: '36px', fontWeight: '600' }}>
              {user?.role} Profile
            </Typography>
          </Box>
          <Paper
            sx={{
              display: 'inline-flex',
              gap: '50px',
              padding: '20px 40px',
            }}
          >
            <Box
              sx={{
                display: 'flex',
                gap: '50px',
                alignItems: 'start',
                justifyContent: 'start',
              }}
            >
              <Paper
                sx={{
                  display: 'flex',
                  padding: '20px 50px',
                  height: '100%',
                }}
              >
                <Box
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    flexDirection: 'column',
                    gap: '16px',
                  }}
                >
                  <Avatar
                    sx={{
                      bgcolor: 'black',
                      color: 'white',
                      border: '1px solid white',
                      width: '150px',
                      height: '150px',
                      fontSize: '128px',
                    }}
                  >
                    {user?.first_name?.substring(0, 1)}
                  </Avatar>
                  <Typography sx={{ fontSize: '18px' }}>
                    {user?.first_name} {user?.last_name}
                  </Typography>
                  <Button variant="outlined" onClick={handleOpen}>
                    Edit Profile
                  </Button>
                </Box>
              </Paper>
            </Box>
            <Box
              sx={{
                display: 'flex',
                flexDirection: 'column',
                gap: '50px',
                width: '100%',
              }}
            >
              <Box
                sx={{
                  display: 'flex',
                  flexDirection: 'column',
                  gap: '16px',
                }}
              >
                <Paper
                  sx={{
                    display: 'flex',
                    flexDirection: 'column',
                    gap: '20px',
                    padding: '20px 50px',
                    width: '100%',
                  }}
                >
                  <Typography sx={{ fontSize: '24px', fontWeight: '600' }}>
                    Personal Info
                  </Typography>
                  <Box
                    sx={{
                      display: 'flex',
                      flexDirection: 'column',
                      gap: '16px',
                    }}
                  >
                    <InputLabel
                      htmlFor="email"
                      sx={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'space-between',
                        gap: '16px',
                        fontSize: '16px',
                        color: 'black',
                      }}
                    >
                      Email
                      <Input
                        id="email"
                        sx={{
                          border: '1px solid black',
                          padding: '5px 10px',
                          borderRadius: '5px',
                          color: 'black',
                          '&.Mui-disabled': {
                            color: 'black',
                            borderColor: 'black',
                          },
                        }}
                        value={user?.email}
                        disabled
                        disableUnderline
                      />
                    </InputLabel>
                    <InputLabel
                      htmlFor="first_name"
                      sx={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'space-between',
                        gap: '16px',
                        fontSize: '16px',
                        color: 'black',
                      }}
                    >
                      First Name
                      <Input
                        id="first_name"
                        sx={{
                          border: '1px solid black',
                          padding: '5px 10px',
                          borderRadius: '5px',
                          color: 'black',
                          '&.Mui-disabled': {
                            color: 'black',
                            borderColor: 'black',
                          },
                        }}
                        value={user?.first_name}
                        disabled
                        disableUnderline
                      />
                    </InputLabel>
                    <InputLabel
                      htmlFor="last_name"
                      sx={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'space-between',
                        gap: '16px',
                        fontSize: '16px',
                        color: 'black',
                      }}
                    >
                      Last Name
                      <Input
                        id="last_name"
                        sx={{
                          border: '1px solid black',
                          padding: '5px 10px',
                          borderRadius: '5px',
                          color: 'black',
                          '&.Mui-disabled': {
                            color: 'black',
                            borderColor: 'black',
                          },
                        }}
                        value={user?.last_name}
                        disabled
                        disableUnderline
                      />
                    </InputLabel>
                    <InputLabel
                      htmlFor="phoneNumber"
                      sx={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'space-between',
                        gap: '16px',
                        fontSize: '16px',
                        color: 'black',
                      }}
                    >
                      Phone Number
                      <Input
                        type="phoneNumber"
                        id="email"
                        sx={{
                          border: '1px solid black',
                          padding: '5px 10px',
                          borderRadius: '5px',
                          color: 'black',
                          '&.Mui-disabled': {
                            color: 'black',
                            borderColor: 'black',
                          },
                        }}
                        value={user?.phone_number}
                        disabled
                        disableUnderline
                      />
                    </InputLabel>
                  </Box>
                </Paper>
              </Box>
            </Box>
          </Paper>
        </Box>
      </div>
      <Modal open={open} onClose={handleClose}>
        <Box
          sx={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            width: 400,
            bgcolor: 'background.paper',
            boxShadow: 24,
            p: 4,
            display: 'flex',
            flexDirection: 'column',
            gap: 2,
          }}
        >
          <Typography variant="h6" component="h2">
            Edit Profile
          </Typography>
          <InputLabel>Email</InputLabel>
          <Input
            value={editUser.email}
            disabled
            disableUnderline
            sx={{
              border: '1px solid black',
              padding: '5px 10px',
              borderRadius: '5px',
            }}
          />
          <InputLabel>First Name</InputLabel>
          <Input
            name="first_name"
            value={editUser.first_name}
            onChange={handleChange}
            disableUnderline
            sx={{
              border: '1px solid black',
              padding: '5px 10px',
              borderRadius: '5px',
            }}
          />
          <InputLabel>Last Name</InputLabel>
          <Input
            name="last_name"
            value={editUser.last_name}
            onChange={handleChange}
            disableUnderline
            sx={{
              border: '1px solid black',
              padding: '5px 10px',
              borderRadius: '5px',
            }}
          />
          <InputLabel>Phone Number</InputLabel>
          <Input
            name="phone"
            value={editUser.phone_number}
            onChange={handleChange}
            disableUnderline
            sx={{
              border: '1px solid black',
              padding: '5px 10px',
              borderRadius: '5px',
            }}
          />
          <Button variant="contained" onClick={handleSave} sx={{ mt: 2 }}>
            Save
          </Button>
        </Box>
      </Modal>
    </Box>
  );
}
