/* eslint-disable no-alert */
import React, { useCallback, useEffect, useState, ChangeEvent } from 'react';
import {
  Box,
  Button,
  Input,
  InputLabel,
  TextField,
  Typography,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import styles from './style.module.scss';
import { useUserStore } from '../../../app/store/useUserStore';

export default function CreateSpace() {
  const { isAuthorized } = useUserStore();
  const navigate = useNavigate();

  const [form, setForm] = useState({
    name: '',
    address: '',
    location: '',
    size: '',
    maxCapacity: '',
    baseRentalCost: '',
  });

  const [selectedFile, setSelectedFile] = useState<File | null>(null);

  const handleChange = (event: { target: { name: string; value: string } }) => {
    const { name, value } = event.target;
    setForm((prevForm) => ({ ...prevForm, [name]: value }));
  };

  const handleFileChange = (event: ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    setSelectedFile(file as File);
  };

  const onSubmit = useCallback(async () => {
    if (!selectedFile) {
      // eslint-disable-next-line no-alert
      alert('Please select an image to upload.');
      return;
    }

    const spaceRequest = {
      name: form.name,
      address: form.address,
      location: form.location,
      size: form.size,
      maxCapacity: form.maxCapacity,
      baseRentalCost: form.baseRentalCost,
    };

    const formData = new FormData();
    formData.append('spaceRequest', JSON.stringify(spaceRequest));
    formData.append('name', form.name);
    formData.append('address', form.address);
    formData.append('location', form.location);
    formData.append('size', form.size);
    formData.append('maxCapacity', form.maxCapacity);
    formData.append('baseRentalCost', form.baseRentalCost);
    formData.append('file', selectedFile);

    try {
      const response = await fetch('https://zenuki.kz/api/v1/space', {
        method: 'POST',
        body: formData, // The Content-Type is automatically set to 'multipart/form-data' when using FormData
      });

      if (!response.ok) {
        throw new Error('Failed to create space');
      }

      const data = await response.json();
      if (data) {
        navigate('/space');
      }
    } catch (error) {
      alert('Error creating space!');
    }
  }, [form, navigate, selectedFile]);

  useEffect(() => {
    if (!isAuthorized) {
      // navigate('/login');
    }
  }, [isAuthorized, navigate]);

  return (
    <section className={styles.createSpacePage}>
      <div className="container">
        <Box className={styles.createSpace__wrapper}>
          <Typography
            sx={{
              fontSize: '28px',
              fontWeight: '600',
              textAlign: 'center',
              marginBottom: '20px',
            }}
          >
            Create Space
          </Typography>

          <InputLabel
            sx={{
              flex: 1,
              display: 'flex',
              flexDirection: 'column',
              color: 'black',
            }}
          >
            Space Name
            <Input
              name="name"
              placeholder="Enter space name"
              disableUnderline
              sx={{
                padding: '5px 10px',
                borderRadius: '5px',
                border: '1px solid #ddd',
              }}
              value={form.name}
              onChange={handleChange}
            />
          </InputLabel>

          <Box sx={{ display: 'flex', gap: '50px' }}>
            <InputLabel
              sx={{
                flex: 1,
                display: 'flex',
                flexDirection: 'column',
                color: 'black',
              }}
            >
              Location
              <TextField
                name="location"
                placeholder="Location"
                value={form.location}
                onChange={handleChange}
                sx={{ marginTop: '8px' }}
              />
            </InputLabel>

            <InputLabel
              sx={{
                flex: 1,
                display: 'flex',
                flexDirection: 'column',
                color: 'black',
              }}
            >
              Address
              <TextField
                name="address"
                placeholder="Address"
                value={form.address}
                onChange={handleChange}
                sx={{ marginTop: '8px' }}
              />
            </InputLabel>
          </Box>

          <Box sx={{ display: 'flex', gap: '50px' }}>
            <InputLabel
              sx={{
                flex: 1,
                display: 'flex',
                flexDirection: 'column',
                color: 'black',
              }}
            >
              Venue Size
              <TextField
                name="size"
                type="number"
                placeholder="Enter venue size"
                value={form.size}
                onChange={handleChange}
                sx={{ marginTop: '8px' }}
              />
            </InputLabel>

            <InputLabel
              sx={{
                flex: 1,
                display: 'flex',
                flexDirection: 'column',
                color: 'black',
              }}
            >
              Max Capacity
              <TextField
                name="maxCapacity"
                type="number"
                placeholder="Enter max capacity"
                value={form.maxCapacity}
                onChange={handleChange}
                sx={{ marginTop: '8px' }}
              />
            </InputLabel>
          </Box>

          <InputLabel
            sx={{
              flex: 1,
              display: 'flex',
              flexDirection: 'column',
              color: 'black',
              marginTop: '20px',
            }}
          >
            Base Rental Cost
            <TextField
              name="baseRentalCost"
              type="number"
              placeholder="Enter base rental cost"
              value={form.baseRentalCost}
              onChange={handleChange}
              sx={{ marginTop: '8px' }}
            />
          </InputLabel>

          <Box>
            <Typography sx={{ fontSize: '18px', marginTop: '20px' }}>
              Upload Space Image
            </Typography>
            <input type="file" onChange={handleFileChange} />
          </Box>

          <Button
            variant="contained"
            color="primary"
            sx={{
              width: '100%',
              marginTop: '30px',
              padding: '10px 0',
              backgroundColor: '#7209B7',
            }}
            onClick={onSubmit}
          >
            Create Space
          </Button>
        </Box>
      </div>
    </section>
  );
}
